import React from "react"
import cx from "classnames"
import { MyUI } from "@macashipo/mlib"
export default function MUIATHeader(props) {
  const enablePageTitleIcon = true
  const configPage = props.configPage
  return (
    <div className="mpat-header-detail app-page-title">
      <div className="page-title-wrapper">
        <div className="page-title-heading">
          <div
            className={cx("page-title-icon", {
              "d-none": !enablePageTitleIcon,
            })}
          >
            <i className={"fa fa-star"} />
          </div>
          <div>{configPage.Title}</div>
        </div>
        <div className="page-title-actions">
          <MyUI {...props} type="admintable_header_buttons" />
        </div>
      </div>
    </div>
  )
}
